import { lazy } from 'react'
// import { Redirect } from 'react-router-dom'

const PagesRoutes = [
  {
    path: '/login',
    component: lazy(() => import('../../views/pages/authentication/Login')),
    layout: 'BlankLayout',
    meta: {
      authRoute: true
    }
  },
  {
    path: '/register',
    component: lazy(() => import('../../views/pages/authentication/Register')),
    layout: 'BlankLayout',
    meta: {
      authRoute: true
    }
  },
  {
    path: '/forgot-password',
    component: lazy(() => import('../../views/pages/authentication/ForgotPassword')),
    layout: 'BlankLayout',
    meta: {
      authRoute: true
    }
  },
  {
    path: '/edit-user/:id',
    component: lazy(() => import('../../views/user/employee/Edit')),
    meta: {
      navLink: '/edit-user'
    }
  },
  {
    path: '/edit-customer/:id',
    component: lazy(() => import('../../views/user/customer/Edit')),
    meta: {
      navLink: '/edit-customer'
    }
  },
  {
    path: '/product/add-product',
    component: lazy(() => import('../../views/product/Add'))
    // meta: {
    //   authRoute: true
    // }
  },
  {
    path: '/edit-product/:id',
    component: lazy(() => import('../../views/product/Edit')),
    meta: {
      navLink: '/edit-product'
    }
  },
  {
    path: '/add-deliverycode',
    component: lazy(() => import('../../views/delivery/Add')),
    meta: {
      navLink: '/add-deliverycode'
    }
  },
  {
    path: '/edit-deliverycode/:id',
    component: lazy(() => import('../../views/delivery/Edit')),
    meta: {
      navLink: '/edit-deliverycode'
    }
  },
  {
    path: '/view-order/:id',
    component: lazy(() => import('../../views/order/View')),
    meta: {
      navLink: '/view-order'
    }
  },
  {
    path: '/add-category',
    component: lazy(() => import('../../views/category/Add')),
    meta: {
      navLink: '/add-category'
    }
  },
  {
    path: '/edit-category/:id',
    component: lazy(() => import('../../views/category/Edit')),
    meta: {
      navLink: '/add-category'
    }
  }
]

export default PagesRoutes
